import React from "react";
import Friend from "./Friend";
import { useSelector } from "react-redux";
import { SearchBtn } from "../../icons";
import ActiveList from "./ActiveList";

const Friendlist = () => {
  const { friendsList } = useSelector((state) => state.user.user);

  return (
    <section className="w-full flex-grow flex gap-2 font-custom items-center flex-col text-center overflow-y-auto scrollbar-thin scrollbar-track-transparent  scrollbar-thumb-transBlack">
      <h3 className="text-xl self-start px-5 py-2 font-extrabold capitalize ">
        chats
      </h3>

      {/* Search bar  */}
      <form className="h-fit  flex items-center w-full sticky top-[-1px] py-1 px-4 z-40">
        <input
          placeholder="Search..."
          className="h-10 p-3 w-full outline-none bg-trans rounded-full border  border-primary"
        />
        <button
          className="h-8 w-10 absolute right-5 text-primary"
          type="submit"
        >
          <SearchBtn />
        </button>
      </form>

      {/* ActiveList */}
      <ActiveList />

      {/* recent chats  */}
      <h3 className="text-xl self-start px-5 font-extrabold capitalize ">
        recent chats
      </h3>

      {friendsList.map((friend, index) => {
        return <Friend key={index} friend={friend} />;
      })}
    </section>
  );
};

export default Friendlist;
