import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { acceptReq } from "../../socket/handleRequests";
import { addfriend, removeReq } from "../../store/features/userslice";

const Request = ({
  sender,
  sender_pk,
  time,
  profile,
  fullName,
  username,
  status,
  _id,
}) => {
  const { user } = useSelector((store) => store.user);
  const dispatch = useDispatch();
  return (
    <div className="h-20 flex w-full py-2 px-4  items-center">
      {/* Friend info */}
      <section className="flex gap-2 items-center  h-full flex-grow">
        <img
          className="h-12 aspect-square rounded-full"
          src={
            profile
              ? JSON.parse(profile).secure_url
              : "https://res.cloudinary.com/ddxaqpvsr/image/upload/f_auto,q_auto/v1/chaton/profiles/drqg4ddqgs0kpg3lpqbe"
          }
          alt=""
        />
        {/* name and last message */}
        <article className="flex text-left flex-col">
          <h5 className="font-bold text-sm capitalize">{fullName}</h5>
          <p className="lowercase text-xs font-medium">{username}</p>
        </article>
      </section>
      <button
        className="p-1 bg-primary rounded-md text-sm"
        onClick={async () =>
          acceptReq(
            {
              sender,
              acceptor: user._id,
              sender_pk,
              _id,
            },
            (reqId, friend) => {
              console.log(reqId, "deleted");
              dispatch(removeReq(reqId));
              dispatch(addfriend(friend));
            }
          )
        }
      >
        accept
      </button>
    </div>
  );
};

export default Request;
