import React, { useEffect } from "react";
import { getChats, setActiveFriend } from "../../store/features/chats";
import { useDispatch } from "react-redux";
import { NavLink } from "react-router-dom";

const Friend = ({ friend }) => {
  const { chatID, friendName, profile } = friend;
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getChats(chatID));
  }, []);
  return (
    <NavLink
      onClick={async () => {
        dispatch(setActiveFriend(friend));
      }}
      to="/chat"
      className="h-20 flex w-full p-2  items-center
      hover:bg-trans"
    >
      {/* Friend info */}
      <section className="flex gap-2 items-center px-1 h-full flex-grow">
        <img
          className="h-12 aspect-square rounded-full"
          src={
            profile
              ? JSON.parse(profile).secure_url
              : "https://res.cloudinary.com/ddxaqpvsr/image/upload/f_auto,q_auto/v1/chaton/profiles/drqg4ddqgs0kpg3lpqbe"
          }
          alt=""
        />
        {/* name and last message */}
        <article className="flex text-left flex-col">
          <h5 className="font-bold text-sm sm:text-md  capitalize">
            {friendName}
          </h5>
          <p className="lowercase text-xs sm:text-sm font-medium">ajao</p>
        </article>
      </section>
    </NavLink>
  );
};

export default Friend;
