import React from "react";
import { useSelector } from "react-redux";

const Chat = ({ msg, time, sender, name }) => {
  const { _id, profile } = useSelector((state) => state.user.user);
  const { activeFriend } = useSelector((state) => state.chats);
  if (sender === _id) {
    return (
      <article className="h-fit min-h-fit flex flex-row-reverse w-fit gap-2 items-center relative animate-sent">
        <img
          src={
            profile
              ? JSON.parse(profile).secure_url
              : "https://res.cloudinary.com/ddxaqpvsr/image/upload/f_auto,q_auto/v1/chaton/profiles/drqg4ddqgs0kpg3lpqbe"
          }
          alt=""
          className="h-10 shadow-md aspect-square rounded-full"
        />
        <section className="h-fit py-2 px-4 w-fit bg-primary text-txtClr shadow-xl rounded-xl max-w-[50vw] ">
          <p className="break-normal text-xs sm:text-sm">{msg}</p>
        </section>
        <p className="text-[10px] text-gray-400">{time}</p>
      </article>
    );
  }
  return (
    <article className="h-fit min-h-fit flex w-fit self-start gap-2 items-center">
      <img
        src={
          profile
            ? JSON.parse(profile).secure_url
            : "https://res.cloudinary.com/ddxaqpvsr/image/upload/f_auto,q_auto/v1/chaton/profiles/drqg4ddqgs0kpg3lpqbe"
        }
        alt=""
        className="h-10 self-start shadow-md aspect-square rounded-full"
      />
      <section className="h-fit py-2 px-4 w-fit bg-trans text-txtClr shadow-xl rounded-xl max-w-[40vw] ">
        <p className="break-normal text-xs sm:text-sm">{msg}</p>
      </section>
      <p className="text-[10px] text-gray-400">{time}</p>
    </article>
  );
};

export default Chat;
