import React, { useState } from "react";
import { SendBtn } from "../../icons";
import socket from "../../socket";
import { useDispatch, useSelector } from "react-redux";
import {
  addMsg,
  addPendingChat,
  removePendingChat,
} from "../../store/features/chats";
const Footer = ({ scrollToBottom }) => {
  const { activeFriend } = useSelector((state) => state.chats);
  const dispatch = useDispatch();
  const [msg, setMsg] = useState("");

  // SEND MESSAGE FUNCTION
  const sendMsg = async (e) => {
    e.preventDefault();
    //IF MSG EMPTY DONNOT SEND
    if (!msg.trim()) return;
    //CREATE A PENDING MSG ID
    const tempId = Date.now();
    //CREATE A MSG PAYLOAD
    const payload = {
      msg,
      chatId: activeFriend.chatID,
      reciever: activeFriend.friendUsername,
    };
    // EMIT MSG
    socket.emit("send_msg", payload, (sent_chat) => {
      dispatch(addMsg({ id: activeFriend.chatID, chat: sent_chat }));
      dispatch(removePendingChat({ id: activeFriend.chatID, tempId }));
    });
    // ADD PENDING CHAT
    dispatch(
      addPendingChat({
        chat: {
          id: tempId,
          msg,
        },
        id: activeFriend.chatID,
      })
    );
    // SCROLL TO BOTTTOM
    scrollToBottom();
    // SET MSG TO NULL
    setMsg("");
  };
  return (
    <footer className="h-14 bg-background py-3 flex justify-center w-full">
      <form
        onSubmit={sendMsg}
        className="flex justify-evenly items-center relative w-11/12 h-10"
      >
        <input
          value={msg}
          onChange={(e) => setMsg(e.target.value)}
          placeholder="type your message...."
          className="h-12 outline-none px-5 bg-trans rounded-full w-full shadow-2xl"
        />
        <button
          className="absolute w-12 px-1 [--txtClr:var(--primary)] h-9 right-0"
          type="submitt"
        >
          <SendBtn />
        </button>
      </form>
    </footer>
  );
};

export default Footer;
