import React from "react";
import logo from "../image/plane-logo.png";

const Hero = ({ homePage }) => {
  const slide = (per) => {
    homePage.current.style.left = "-100%";
  };

  return (
    <div className="flex flex-col relative z-10 bg-background items-center justify-evenly w-[100vw] h-full">
      {/* <img src={logo} className="h-32" alt="" /> */}
      <header className="h-16 w-full absolute top-0 p-2"> </header>
      <section className="flex h-1/2 gap-6 flex-col items-center">
        <img src={logo} className="h-32  "></img>
        <article className="flex flex-col items-center">
          <h1 className=" text-primary text-2xl font-extrabold uppercase">
            chat on
          </h1>
          <p className=" text-sm  sm:text-md capitalize font-semibold ">
            privacy at its best
          </p>
          <p className="w-2/3 min-w-[200px] text-center text-xs sm:text-sm text-gray-400">
            Introducing Chat on - your private chat companion. Chat one-on-one
            verified for safety. Enjoy secure messaging now. Your privacy, our
            priority.
          </p>
        </article>
        <button
          onClick={() => slide()}
          className="bg-gradient-to-br to-primary from-[#6be0fa] px-4 py-2 rounded-full uppercase font-semibold"
        >
          get started
        </button>
      </section>
    </div>
  );
};

export default Hero;

