import React, { useEffect, useRef, useState } from "react";
import SidePanel from "./SIdePanel";
import ChatPanel from "./chatPanel";
import { useDispatch } from "react-redux";
import RequestPage from "./friendReq/requests";
import socket from "../socket";
import {
  requestRecieve,
  addfriend,
  removePendingReq,
} from "../store/features/userslice";
import { msgRecieve } from "../store/features/chats";
import ProfilePanel from "./profile";
import { Route, Routes } from "react-router-dom";
const MainApp = () => {
  const dispatch = useDispatch();
  //VARIOUS SOCET LISTENERS
  useEffect(() => {
    //LISTEN FOR REQUEST RECIEVE
    socket.on("req_recieve", (req) => {
      dispatch(requestRecieve(req));
    });
    //LISTEN FOR REQUEST ACCEPTED
    socket.on("req_accepted", (reqId, friend) => {
      dispatch(removePendingReq(reqId));
      dispatch(addfriend(friend));
    });
    //LISTEN FOR MESSAGE RECIEVE
    socket.on("msg_recieve", (payload) => {
      dispatch(msgRecieve(payload));
    });
  }, []);

  const chatPanel = useRef();
  return (
    <main className="h-screen overflow-hidden relative w-screen sm:flex">
      <SidePanel />

      <Routes>
        <Route path="/" element={<div className="hidden"></div>} />
        <Route path="/chat" element={<ChatPanel />}></Route>
        <Route path="/requests" element={<RequestPage />}></Route>
        <Route path="/profile" element={<ProfilePanel />}></Route>
      </Routes>
    </main>
  );
};

export default MainApp;

