import React from "react";
import { AddFriend } from "../../icons";
import { useSelector } from "react-redux";
import { NavLink } from "react-router-dom";
const Header = () => {
  const { profile, requestlist } = useSelector((store) => store.user.user);
  return (
    <header className="w-full h-16 items-center justify-between px-2 uppercase flex">
      {/* logo and app name  */}
      <div className="flex gap-1 flex-grow h-full items-center">
        {/* <article className="h-full text-center"> */}
        {/* <img src={logo} className="h-full " alt="" /> */}
        <h1 className="text-2xl text-primary font-extrabold">chat on</h1>
        {/* </article> */}
      </div>

      {/* profile section  */}
      <div className="font-light  gap-2 flex items-center h-[90%]">
        <NavLink
          // onClick={() => setRequest(true)}
          to="/requests"
          className="h-8 w-12 hover:active:bg-none relative "
        >
          {requestlist.length > 0 && (
            <span className="bg-primary w-4 rounded-full bottom-[-4px] right-0 absolute text-center text-xs">
              {requestlist.length}
            </span>
          )}
          <AddFriend />
        </NavLink>
        {/* profile photo */}
        <NavLink to="/profile">
          <img
            className="h-12 aspect-square rounded-full "
            src={
              profile
                ? JSON.parse(profile).secure_url
                : "https://res.cloudinary.com/ddxaqpvsr/image/upload/f_auto,q_auto/v1/chaton/profiles/drqg4ddqgs0kpg3lpqbe"
            }
            alt=""
          />
        </NavLink>
      </div>
    </header>
  );
};

export default Header;
