import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { URL, register } from "../../axios/auht";
import axios from "axios";
import {
  closeErrorModal,
  closeLoadingModal,
  showErrorModal,
  showLoadingModal,
} from "./modals";
import socket from "../../socket";

//LOGIN USER FUNCTION
export const login = createAsyncThunk("user/login", async (user, thunkAPI) => {
  try {
    thunkAPI.dispatch(showLoadingModal("logging in..."));
    const subscription = localStorage.getItem("noti_endPoint");
    if (subscription) {
      user = { ...user, notiEndpoint: subscription };
    }
    const resp = await register("login", user);
    thunkAPI.dispatch(setUser(resp));
    thunkAPI.dispatch(closeLoadingModal());
    socket.connect();
    await localStorage.setItem("user", JSON.stringify(resp.user));
  } catch (error) {
    thunkAPI.dispatch(closeLoadingModal());
    if (error.message === "Network Error") {
      thunkAPI.dispatch(showErrorModal(error.message));
      setTimeout(() => thunkAPI.dispatch(closeErrorModal()), 4000);
      return;
    }
    thunkAPI.dispatch(showErrorModal(error.response.data.message));
    setTimeout(() => thunkAPI.dispatch(closeErrorModal()), 4000);
  }
});
// REGISTER USER FUNCTION
export const registerUser = createAsyncThunk(
  "user/register",
  async (user, thunkAPI) => {
    try {
      thunkAPI.dispatch(showLoadingModal("regisering..."));
      const subscription = localStorage.getItem("noti_endPoint");
      if (subscription) {
        user = { ...user, notiEndpoint: subscription };
      }
      const resp = await register("register", user);
      thunkAPI.dispatch(setUser(resp));
      thunkAPI.dispatch(closeLoadingModal());
      socket.connect();
      await localStorage.setItem("user", JSON.stringify(resp.user));
      return;
    } catch (error) {
      thunkAPI.dispatch(closeLoadingModal());
      if (error.message === "Network Error") {
        thunkAPI.dispatch(showErrorModal(error.message));
        setTimeout(() => thunkAPI.dispatch(closeErrorModal()), 4000);
        return;
      }
      thunkAPI.dispatch(showErrorModal(error.response.data.message));
      setTimeout(() => thunkAPI.dispatch(closeErrorModal()), 4000);
    }
  }
);
// VERIFY USER FUNCTION
export const verifyUser = createAsyncThunk(
  "verify/user",
  async (token, thunkAPI) => {
    try {
      const userLocal = JSON.parse(localStorage.getItem("user"));
      thunkAPI.dispatch(
        setUser({ token: token.split(" ")[1], user: userLocal })
      );
      socket.connect();
      const resp = await axios(`${URL}/user/login`, {
        headers: {
          Accept: "application/json",
          Authorization: token,
        },
      });
      return thunkAPI.dispatch(setUser(resp.data));
    } catch (error) {
      if (error.message === "Network Error") {
        thunkAPI.dispatch(showErrorModal(error.message));
        thunkAPI.dispatch(setOfflineUser());
        setTimeout(() => thunkAPI.dispatch(closeErrorModal()), 4000);
        return;
      }
      thunkAPI.dispatch(logoutUser());
      thunkAPI.dispatch(showErrorModal(error.response.data.message));
      setTimeout(() => thunkAPI.dispatch(closeErrorModal()), 4000);
      return thunkAPI.dispatch(logoutUser());
    }
  }
);

//INITIAL STATE
const initialState = {
  user: {
    fullName: "",
    username: "",
    dob: "",
    email: "",
    requestlist: [],
    recentlySearched: [],
    friendsList: [],
  },
  isLoggedIn: false,
};

// USER SLICE
const userSlice = createSlice({
  name: "user",
  initialState,
  reducers: {
    // SETUSER
    setUser: (store, { payload }) => {
      const { token, user } = payload;
      localStorage.setItem("token", `Bearer ${token}`);
      localStorage.setItem("user", JSON.stringify(user));
      store.user = { ...user };
      store.isLoggedIn = true;
    },
    //LOGOUT USER
    logoutUser: (state) => {
      localStorage.removeItem("token");
      localStorage.removeItem("user");
      state.user = initialState;
      state.isLoggedIn = false;
    },
    // REQUEST RECIEVE
    requestRecieve: (state, { payload }) => {
      if (
        !state.user.requestlist.some((req) => req.sender === payload.sender)
      ) {
        state.user.requestlist = [...state.user.requestlist, payload];
      }
      state.user.requestlist = [...state.user.requestlist, payload];
    },
    //ADD PENDING REQUEST
    addPendingReq: (state, { payload }) => {
      // console.log("added pending request");
      if (
        !state.user.pendingreq.some((req) => req.acceptor === payload.acceptor)
      ) {
        state.user.pendingreq = [...state.user.pendingreq, payload];
      }
    },
    //REMOVE PENDING REQUEST
    removePendingReq: (state, { payload }) => {
      // console.log(payload, "removed pend req");
      state.user.pendingreq = state.user.pendingreq.filter(
        (req) => req._id !== payload
      );
    },
    // ADD FRIEND
    addfriend: (state, { payload }) => {
      state.user.friendsList.push(payload);
    },
    //REMOVE REQUEST
    removeReq: (state, { payload }) => {
      // console.log(payload, "removed request");
      state.user.requestlist = state.user.requestlist.filter(
        (req) => req._id !== payload
      );
    },
    setOfflineUser: (state) => {
      const user = JSON.parse(localStorage.getItem("user"));
      state.user = user;
      state.isLoggedIn = true;
    },
  },
});

export const {
  setUser,
  logoutUser,
  requestRecieve,
  requestAccepted,
  addPendingReq,
  removePendingReq,
  removeReq,
  addfriend,
  setOfflineUser,
} = userSlice.actions;

export default userSlice.reducer;
