import React from "react";
import { useSelector } from "react-redux";
import { SendBtn } from "../../icons";

const TempChat = ({ msg }) => {
  const { profile } = useSelector((state) => state.user.user);
  return (
    <div>
      <article className="h-fit min-h-fit flex flex-row-reverse w-fit gap-2 items-center">
        <p className="h-2 w-2 [--txtClr:var(--primary)]">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-4 h-4"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M6 12L3.269 3.126A59.768 59.768 0 0121.485 12 59.77 59.77 0 013.27 20.876L5.999 12zm0 0h7.5"
            />
          </svg>
        </p>
        <img
          src={
            profile
              ? JSON.parse(profile).secure_url
              : "https://res.cloudinary.com/ddxaqpvsr/image/upload/f_auto,q_auto/v1/chaton/profiles/drqg4ddqgs0kpg3lpqbe"
          }
          alt=""
          className="h-10 shadow-md aspect-square rounded-full"
        />
        <section className="h-fit py-2 px-4 w-fit bg-primary text-txtClr shadow-xl rounded-xl max-w-[50vw] ">
          <p className="break-normal text-xs sm:text-sm">{msg}</p>
        </section>
      </article>
    </div>
  );
};

export default TempChat;
