import React, { useEffect } from "react";
import { addPendingReq } from "../../store/features/userslice";
import { AddFriend } from "../../icons";
import { useDispatch, useSelector } from "react-redux";
import { sendFriendReq } from "../../socket/handleRequests";
import Request from "./request";
import PendingReq from "./pendingreq";
import Friend from "../SIdePanel/Friend";

const Searched = ({ profile, fullName, friend, _id, chatPanel }) => {
  const dispatch = useDispatch();
  const { user } = useSelector((store) => store.user);

  //CHECK IF SEARCHED IS ALREADY FRIEND
  const alreadyFried = user.friendsList.find(
    (frnd) => frnd.friendUsername === friend
  );
  if (alreadyFried) {
    return <Friend chatPanel={chatPanel} friend={alreadyFried} />;
  }

  //CHECK IF REQUEST ALREADY PRESENT
  const requestExist = user.requestlist.find((request) => {
    return request.sender === _id;
  });
  if (requestExist) {
    return <Request {...requestExist} key={_id} />;
  }
  //CHECK IF REQUEST ALREADY SENT
  const pendingExist = user.pendingreq.find((req) => {
    return req.acceptor === _id;
  });

  if (pendingExist) {
    return <PendingReq key={_id} {...pendingExist} />;
  }

  return (
    <div className="h-20 flex w-full py-2 px-4  items-center">
      {/* Friend info */}
      <section className="flex gap-2 items-center  h-full flex-grow">
        <img
          className="h-12 aspect-square rounded-full"
          src={
            profile
              ? JSON.parse(profile).secure_url
              : "https://res.cloudinary.com/ddxaqpvsr/image/upload/f_auto,q_auto/v1/chaton/profiles/drqg4ddqgs0kpg3lpqbe"
          }
          alt=""
        />
        {/* name and last message */}
        <article className="flex text-left flex-col">
          <h5 className="font-bold text-md capitalize">{fullName}</h5>
          <p className="lowercase text-sm font-medium">{friend}</p>
        </article>
      </section>
      <button
        className="h-8 w-9"
        onClick={async () => {
          console.log(user);
          sendFriendReq(
            {
              sender: user._id,
              acceptor: _id,
            },
            (req) => {
              dispatch(addPendingReq(req));
            }
          );
        }}
      >
        <AddFriend />
      </button>
    </div>
  );
};

export default Searched;
