import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { login } from "../store/features/userslice";
const Login = ({ authPanel }) => {
  const [user, setuser] = useState({ username: "", password: "" });
  const dispatch = useDispatch();
  const slide = () => {
    authPanel.current.style.left = `-100%`;
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    dispatch(login(user));
  };
  return (
    <div className="w-[100vw] h-full flex flex-col justify-center items-center">
      <section
        className="h-fit gap-5 w-3/4 relative bg-secondary border border-primary shadow-3xl rounded-lg text-black
      flex flex-col items-center pb-5"
      >
        <h5 className="text-lg sm:text-2xl  font-extrabold py-2 uppercase">
          login
        </h5>

        <form
          onSubmit={handleSubmit}
          className="flex flex-col gap-5 h-fit w-full"
        >
          <section className="flex w-full flex-col h-fit items-center gap-5">
            <input
              type="text"
              value={user.name}
              className="bg-transparent border text-sm px-3 py-1 border-gray-400 max-w-[90%] rounded-full placeholder:text-txtClr"
              placeholder="Username..."
              onChange={(e) => {
                setuser({ ...user, username: e.target.value });
              }}
            />
            <input
              type="text"
              value={user.password}
              className="bg-transparent border text-sm px-3 py-1 border-gray-400 max-w-[90%] rounded-full placeholder:text-txtClr"
              placeholder="Password..."
              onChange={(e) => {
                setuser({ ...user, password: e.target.value });
              }}
            />
          </section>

          <section className="flex flex-col gap-2  items-center">
            <button
              className="text-xs  font-semibold text-primary"
              type="button"
            >
              Forgot Password?
            </button>
            <button
              className="bg-primary text-white sm:text-sm text-xs font-bold px-8 rounded-md py-2"
              type="submit"
            >
              Login
            </button>

            <p className="text-xs text-center font-semibold">
              Don't have an account?{" "}
              <button className="text-primary" onClick={slide} type="button">
                Register
              </button>
            </p>
          </section>
        </form>
      </section>
    </div>
  );
};

export default Login;
