import React, { useEffect } from "react";
import Chat from "./Chat";
import TempChat from "./tempChat";

const Chats = ({ activeFriend, bottom }) => {
  useEffect(() => {
    bottom.current.scrollIntoView();
  }, []);
  return (
    <main className=" overflow-y-auto w-full items-end scrollbar-thin  scrollbar-thumb-transBlack p-3 flex flex-col gap-4 flex-grow">
      {activeFriend.chats.map((chat, i) => (
        <Chat {...chat} key={i} />
      ))}
      {activeFriend.pendingChats.map((chat) => {
        return <TempChat {...chat} key={chat.id} />;
      })}

      <div className="w-full " ref={bottom}></div>
    </main>
  );
};

export default Chats;
