import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { URL } from "../../axios/auht";

export const getChats = createAsyncThunk(
  "getchats",
  async (chatId, thunkAPI) => {
    try {
      const token = localStorage.getItem("token");
      // GET CHATS
      const response = await axios(`${URL}/friends/getchats/${chatId}`, {
        headers: {
          Accept: "application/json",
          Authorization: token,
        },
      });

      thunkAPI.dispatch(getChat(response.data));
      const OfflineChats = JSON.parse(localStorage.getItem("chats"));
      return localStorage.setItem(
        "chats",
        JSON.stringify([
          ...OfflineChats,
          { ...response.data, pendingChats: [] },
        ])
      );
    } catch (error) {
      if (error.message === "Network Error") {
        thunkAPI.dispatch(setOfflineChats());
        return;
      }
    }
  }
);

const chatsSlice = createSlice({
  initialState: {
    chats: [],
    activeFriend: {
      chatID: "",
      friendUsername: "",
      friendName: "",
      profile: "",
      blocked: false,
      _id: "",
      chats: [],
      pendingChats: [],
    },
  },
  name: "chats",
  reducers: {
    getChat: (state, { payload }) => {
      const chatPresent = state.chats.find((chat) => chat._id === payload._id);
      if (chatPresent) {
        chatPresent = { ...payload, pendingChats: chatPresent.pendingChats };
      } else {
        payload.pendingChats = [];
        state.chats = [...state.chats, payload];
      }
    },
    setActiveFriend: (state, { payload }) => {
      state.activeFriend = {
        ...payload,
        chats: state.chats.find((chat) => chat._id === payload.chatID).chats,
        pendingChats: state.chats.find((chat) => chat._id === payload.chatID)
          .pendingChats,
      };

      // state.chats.find((chat) => chat._id === payload);
    },
    addMsg: (state, { payload }) => {
      if (payload.id === state.activeFriend.chatID) {
        state.activeFriend.chats.push(payload.chat);
      }
      state.chats
        .find((chat) => chat._id === payload.id)
        .chats.push(payload.chat);
    },
    msgRecieve: (state, { payload }) => {
      console.log(payload);

      state.chats
        .find((chat) => chat._id === payload.chatId)
        .chats.push(payload.recievedMsg);
      if (state.activeFriend.chatID === payload.chatId)
        state.activeFriend.chats.push(payload.recievedMsg);
    },
    addPendingChat: (state, { payload }) => {
      state.activeFriend.pendingChats.push(payload.chat);
      state.chats
        .find((chat) => chat._id === payload.id)
        .pendingChats.push(payload.chat);
    },
    removePendingChat: (state, { payload }) => {
      state.activeFriend.pendingChats = state.activeFriend.pendingChats.filter(
        (chat) => {
          return chat.id !== payload.tempId;
        }
      );
      state.chats.find((chat) => chat._id === payload.id).pendingChats =
        state.chats
          .find((chat) => chat._id === payload.id)
          .pendingChats.filter((chat) => chat.id !== payload.tempId);
    },
    setOfflineChats: (state) => {
      state.chats = JSON.parse(localStorage.getItem("chats"));
    },
  },

  extraReducers: {},
});
export const {
  getChat,
  setActiveFriend,
  addMsg,
  msgRecieve,
  addPendingChat,
  removePendingChat,
  setOfflineChats,
} = chatsSlice.actions;

export default chatsSlice.reducer;
