import { createSlice } from "@reduxjs/toolkit";

const modalSlice = createSlice({
  name: "modals",
  initialState: {
    errorModal: {
      show: false,
      message: "",
    },
    loadingModal: {
      show: false,
      message: "",
    },
  },
  reducers: {
    showLoadingModal: (state, { payload }) => {
      state.loadingModal.show = true;
      state.loadingModal.message = payload;
    },
    showErrorModal: (state, { payload }) => {
      state.errorModal.show = true;
      state.errorModal.message = payload;
    },
    closeLoadingModal: (state) => {
      state.loadingModal.show = false;
      state.loadingModal.message = "";
    },
    closeErrorModal: (state) => {
      state.errorModal.show = false;
      state.errorModal.message = "";
    },
  },
});
export const {
  showErrorModal,
  closeErrorModal,
  showLoadingModal,
  closeLoadingModal,
} = modalSlice.actions;

export default modalSlice.reducer;
