import friendrofile from "../../image/profile.jpg";
import React from "react";

const ActiveList = () => {
  return (
    <div className="h-fit scrollbar-none w-11/12 p-3 shrink-0 overflow-x-auto flex items-center">
      <ActiveUser key={1} />
      <ActiveUser key={2} />
      <ActiveUser key={3} />
      <ActiveUser key={5} />
      <ActiveUser key={8} />
      <ActiveUser key={9} />
      <ActiveUser key={80} />
      <ActiveUser key={99} />
      <ActiveUser key={990} />
      <ActiveUser key={767} />
    </div>
  );
};

const ActiveUser = () => {
  return (
    <article className="h-fit shrink-0 gap-1 items-center flex flex-col relative w-20">
      <section className="relative">
        <img className="h-12  rounded-full" src={friendrofile} alt="" />
        <span className="absolute bottom-0 right-0 rounded-full h-3 w-3 bg-primary"></span>
      </section>
      <p className="text-sm">Arslaan</p>
    </article>
  );
};

export default ActiveList;
