import React, { useRef, useState } from "react";

import Header from "./Header";
import Footer from "./Footer";
import { useSelector } from "react-redux";
import Chats from "./chats";

const ChatPanel = () => {
  const { activeFriend } = useSelector((state) => state.chats);
  const bottom = useRef();

  const scrollToBottom = async () => {
    bottom.current.scrollIntoView({ behavior: "smooth" });
  };
  if (activeFriend.chatID === "")
    return (
      <section className="sm:w-3/4  hidden bg-background w-full h-full absolute sm:static z-50 sm:flex items-stretch flex-col flex-1 transition-all">
        <h2 className=" my-[auto] text-center  text-gray-400 text-2xl ">
          select a chat to start a conversation
        </h2>
      </section>
    );

  return (
    <section className="sm:w-3/4 animate-slideLeft top-0 left-0 sm:static  bg-background w-full h-full absolute z-50 flex items-stretch flex-col flex-1">
      <Header />
      {/* chats */}
      <Chats bottom={bottom} activeFriend={activeFriend}></Chats>
      {/* input box  */}
      <Footer scrollToBottom={scrollToBottom} />
    </section>
  );

  // {/* header  */}
  // activeFriend.chatID !== "" ? return (
  // <section className="sm:w-3/4 bg-background w-full h-full absolute z-50 flex items-stretch flex-col flex-1 transition-all">
  //     <Header />
  //     {/* chats */}
  //     <Chats bottom={bottom} activeFriend={activeFriend}></Chats>
  //     {/* input box  */}
  //     <Footer scrollToBottom={scrollToBottom} />
  //   </section>
  // ) : return(
  //   <section className="sm:w-3/4 bg-background w-full h-full absolute z-50 flex items-stretch flex-col flex-1 transition-all">
  //   <h2 className=" my-[auto] text-center  text-gray-400 text-2xl ">
  //     select a chat to start a conversation
  //   </h2>
  //   </section>
  // )
};

export default ChatPanel;
