import React, { useRef } from "react";
import logo from "../image/plane-logo.png";
import { HomeIcon } from "../icons";
import Login from "./Login";
import Register from "./register-02";

const Auth = ({ homePage }) => {
  const authPanel = useRef();
  return (
    <div className="w-screen h-screen relative">
      <header className="h-fit absolute top-0 z-50 w-screen p-4 flex items-center justify-between">
        {/* //logo and name  */}
        <section className="flex h-8 items-center gap-2">
          <img src={logo} className="h-8 sm:h-10"></img>
          <h1 className="text-xl uppercase text-primary">chat on</h1>
        </section>
        {/* HOME BTN  */}
        <button
          className="h-6 "
          onClick={() => (homePage.current.style.left = `0`)}
        >
          <HomeIcon />
        </button>
      </header>
      <main
        ref={authPanel}
        className="h-full flex absolute left-[0]  bg-transBlack transition-all aspect-[1/3] w-[200vw]"
      >
        <Login authPanel={authPanel} />
        <Register authPanel={authPanel} />
      </main>
    </div>
  );
};

export default Auth;
