import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { ArrowLeft, Birthday, Edit, Logout, Mail, User } from "../../icons";
import { logoutUser } from "../../store/features/userslice";
import EditProfile from "./edit";
import { NavLink } from "react-router-dom";

const ProfilePanel = () => {
  const { _id, dob, username, fullName, email, profile } = useSelector(
    (state) => state.user.user
  );
  const dispatch = useDispatch();
  return (
    // bg-gradient-to-br from-indigo-700 to-purple-500 via-primary

    <div className=" flex flex-col gap-8 items-center justify-center bg-background h-screen w-screen absolute top-0 z-50">
      <header className="h-12 p-2 w-full absolute top-0 flex items-center justify-between ">
        <NavLink to="/" replace className="w-8 h-8 ">
          <ArrowLeft />
        </NavLink>

        <button
          onClick={() => dispatch(logoutUser())}
          className="h-8 flex items-center justify-center text-xs [--txtClr:red]"
        >
          <p>Logout</p>
          <Logout></Logout>
        </button>
      </header>
      <main className="h-3/5 w-3/4 relative px-2 shadow-lg shadow-primary bg-background flex flex-col items-center justify-around rounded-3xl">
        <div className="relative  w-5/6">
          <div className="flex absolute translate-y-[-50%] top-0 flex-col items-center gap-2">
            <img
              className="w-1/2 border-2 border-primary   aspect-square rounded-full"
              src={
                profile
                  ? JSON.parse(profile).secure_url
                  : "https://res.cloudinary.com/ddxaqpvsr/image/upload/f_auto,q_auto/v1/chaton/profiles/drqg4ddqgs0kpg3lpqbe"
              }
              alt=""
            />
            <h2 className="text-lg capitalize text-primary">{fullName}</h2>
          </div>
        </div>
        <section className="p-4 w-full items-center flex gap-4 flex-col ">
          {/* //INFORMATION CARD {USERNAME} */}
          <article className="w-full  p-2 bg-secondary flex items-center gap-4 rounded-md shadow-sm shadow-primary">
            <section className="h-6 [--txtClr:var(--primary)] ">
              <User></User>
            </section>
            <section>
              <p className="text-[10px] text-gray-600">username</p>
              <h5 className="text-sm text-primary">{username}</h5>
            </section>
          </article>
          {/* //INFORMATION CARD {E-MAIL} */}
          <article className="w-full  p-2 bg-secondary flex items-center gap-4 rounded-md shadow-sm shadow-primary">
            <section className="w-6 [--txtClr:var(--primary)] ">
              <Mail></Mail>
            </section>
            <section>
              <p className="text-[10px] text-gray-600">e-mail</p>
              <h5 className="text-xs text-primary">{email}</h5>
            </section>
          </article>
          {/* //INFORMATION CARD {birthdate} */}
          <article className="w-full  p-2 bg-secondary flex items-center gap-4 rounded-md shadow-sm shadow-primary">
            <section className="h-6 [--txtClr:var(--primary)] ">
              <Birthday></Birthday>
            </section>
            <section>
              <p className="text-[10px] text-gray-600">Birthday</p>
              <h5 className="text-sm text-primary">{dob.slice(0, 10)}</h5>
            </section>
          </article>
        </section>
      </main>
      <section className="[--txtClr:var(--primary)] flex items-center justify-center gap-2 h-6 text-lg">
        <button onClick={() => dispatch(logoutUser())} className="h-6 ">
          <Edit></Edit>
        </button>
        <p>Edit Profile</p>
      </section>
    </div>
  );
};

export default ProfilePanel;
