import React from "react";
import Header from "./header";
import Friendlist from "./Friendlist";

const SidePanel = ({ setRequest }) => {
  return (
    <aside className="z-10 absolute bg-background sm:relative flex flex-col sm:border-transBlack sm:border-r-[1px] w-full sm:w-1/4 h-[100vh]">
      <Header setRequest={setRequest} />
      <Friendlist />
    </aside>
  );
};

export default SidePanel;
