import React, { useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";

import { registerUser } from "../store/features/userslice";
import { Photo } from "../icons";
import { closeErrorModal, showErrorModal } from "../store/features/modals";

const Register = ({ authPanel }) => {
  const dispatch = useDispatch();
  const image = useRef();
  const [user, setuser] = useState({
    profile: "",
    fullName: "",
    username: "",
    password: "",
    confirmPass: "",
    email: "",
    dob: "",
  });
  const handleSubmit = (e) => {
    e.preventDefault();
    if (!user.fullName) {
      dispatch(showErrorModal("please enter your fullname"));
      return setTimeout(() => dispatch(closeErrorModal()), 4000);
    }
    if (!user.username) {
      dispatch(showErrorModal("please enter your username"));
      return setTimeout(() => dispatch(closeErrorModal()), 4000);
    }
    if (user.password.length < 8) {
      dispatch(showErrorModal("password must be atleat 8 characters long"));
      return setTimeout(() => dispatch(closeErrorModal()), 4000);
    }
    // check if passwords are distinct
    if (user.password !== user.confirmPass) {
      dispatch(showErrorModal("passwords do not match"));
      setTimeout(() => dispatch(closeErrorModal()), 4000);
      return;
    }
    if (!user.email) {
      dispatch(showErrorModal("please enter your email"));
      setTimeout(() => dispatch(closeErrorModal()), 4000);
      return;
    }
    if (!user.dob) {
      dispatch(showErrorModal("please enter you DOB"));
      setTimeout(() => dispatch(closeErrorModal()), 4000);
      return;
    }
    dispatch(registerUser(user));
  };
  return (
    <div className="w-screen h-screen relative">
      <main className=" rounded-md flex flex-col items-center justify-center h-5/6 pt-14 ">
        <div className="grid md:grid-cols-2 grid-cols-1 p-5 gap-5 justify-center items-center w-fit">
          <section className="flex flex-col min-w-full items-center justify-evenly h-32 sm:col-span-2">
            <label
              htmlFor="profile"
              className="h-3/4 text-gray-400 text-center aspect-square flex items-center justify-center"
            >
              {user.profile ? (
                <img
                  alt=""
                  className="border h-full aspect-square shadow-xl border-primary rounded-full"
                  src={user.profile}
                />
              ) : (
                <Photo />
              )}
            </label>
            <label
              htmlFor="profile"
              className="text-xs capitalize text-gray-400 "
            >
              {user.profile ? "tap to change" : "add a profile photo"}
            </label>
            <input
              type="file"
              id="profile"
              accept="image/png, image/gif, image/jpeg"
              ref={image}
              onChange={function (e) {
                const reader = new FileReader();
                reader.readAsDataURL(e.target.files[0]);
                reader.onloadend = () => {
                  setuser({
                    ...user,
                    profile: reader.result,
                    // profile: URL.createObjectURL(reader.result),
                  });
                };
                reader.onerror = () => {
                  console.log(reader.error);
                };
              }}
              className=" absolute opacity-0"
            />
          </section>
          <input
            required
            className="max-w-[250px] rounded-full bg-transparent border border-primary focus:outline-none px-4 py-1 text-sm sm:text-base placeholder:text-transWhite placeholder:capitalize"
            placeholder="fullname..."
            onChange={(e) => {
              setuser({ ...user, fullName: e.target.value });
            }}
          />
          <input
            required
            className="max-w-[250px] rounded-full bg-transparent border border-primary focus:outline-none px-4 py-1 text-sm sm:text-base placeholder:text-transWhite placeholder:capitalize"
            placeholder="username..."
            onChange={(e) => {
              setuser({ ...user, username: e.target.value });
            }}
          />
          <input
            required
            className="max-w-[250px] rounded-full bg-transparent border border-primary focus:outline-none px-4 py-1 text-sm sm:text-base placeholder:text-transWhite placeholder:capitalize"
            placeholder="password..."
            onChange={(e) => {
              setuser({ ...user, password: e.target.value });
            }}
          />
          <input
            required
            className="max-w-[250px] rounded-full bg-transparent border border-primary focus:outline-none px-4 py-1 text-sm sm:text-base placeholder:text-transWhite placeholder:capitalize"
            placeholder="confirm password..."
            onChange={(e) => {
              setuser({ ...user, confirmPass: e.target.value });
            }}
          />
          <input
            required
            className="max-w-[250px] rounded-full bg-transparent border border-primary focus:outline-none px-4 py-1 text-sm sm:text-base placeholder:text-transWhite placeholder:capitalize"
            placeholder="email..."
            onChange={(e) => {
              setuser({ ...user, email: e.target.value });
            }}
          />
          <input
            required
            type="date"
            className="max-w-[250px] rounded-full bg-transparent border border-primary focus:outline-none px-4 py-1 text-sm sm:text-base placeholder:text-transWhite placeholder:capitalize"
            placeholder="birthdate"
            onChange={(e) => {
              setuser({ ...user, dob: e.target.value });
            }}
          />

          {/* FOOTER  */}
          <footer className="absolute bottom-0 left-0 w-full flex justify-around items-center border-t border-gray-500 bg-background  p-4">
            <button
              className="text-primary text-xs sm:text-sm uppercase font-semibold tracking-wider "
              onClick={() => {
                authPanel.current.style.left = `0`;
              }}
            >
              log in
            </button>
            <button
              className="bg-primary text-white uppercase sm:text-sm text-xs font-semibold tracking-wider px-4 rounded-md py-2"
              onClick={handleSubmit}
            >
              register
            </button>
          </footer>
        </div>
      </main>
    </div>
  );
};

export default Register;
