import React, { useState } from "react";

import { ArrowLeft, SearchBtn } from "../../icons";
import { friendFetch } from "../../axios/friend";
import { useDispatch, useSelector } from "react-redux";
import Searched from "./Searched";
import Request from "./request";
import PendingReq from "./pendingreq";
import { NavLink } from "react-router-dom";
import { closeErrorModal, showErrorModal } from "../../store/features/modals";

const RequestPage = ({ setRequest, chatPanel }) => {
  const dispatch = useDispatch();
  const { recentlySearched, requestlist, pendingreq } = useSelector(
    (store) => store.user.user
  );
  const [searchResults, setSearchResults] = useState();
  const [searcInput, setSearchInput] = useState("");
  const searchFriend = async (e) => {
    e.preventDefault();
    try {
      const friend = await friendFetch.post("/search", {
        username: searcInput,
      });
      setSearchResults(friend.data);
    } catch (error) {
      if (error.message === "Network Error") {
        dispatch(showErrorModal("connection error"));
        setTimeout(() => dispatch(closeErrorModal()), 4000);
        return;
      }
      dispatch(showErrorModal(error.response.data.message));
      setTimeout(() => dispatch(closeErrorModal()), 4000);
    }
  };
  return (
    <div className="h-screen w-screen absolute z-30 bg-background">
      <header className="w-full h-16 flex items-center gap-2 py-2 border-b border-gray-600">
        {/* PROFILE SECTION  onClick={() => setRequest(false)} */}
        <NavLink to="/" replace className="w-8 h-8">
          <ArrowLeft />
        </NavLink>
        <article className=" justify-evenly text-sm flex flex-col">
          <h5 className="text-base font-bold">Friend requests</h5>
        </article>
      </header>
      <main className="w-full h-[calc(100%-64px)]">
        {/* //////////////////   SEARCH BAR    ///////////////// */}
        <form className="h-fit flex items-center bg-background w-full sticky top-[-1px] py-2 px-4 z-40">
          <input
            placeholder="Search..."
            className="h-10 p-3 w-full  outline-none rounded-md border bg-secondary  border-primary"
            value={searcInput}
            onChange={(e) => setSearchInput(e.target.value)}
          />
          <button
            className="h-6 w-8 absolute [--txtClr:var(--primary)] right-5 text-primary"
            type="submit"
            onClick={searchFriend}
          >
            <SearchBtn />
          </button>
        </form>
        {/* search section///////////////////////////////////////// */}
        <section className="h-[calc(100%-55px)] flex flex-col gap-2 py-2 px-2 w-full overflow-scroll scrollbar-thin scrollbar-track-transparent scrollbar-thumb-gray-300 ">
          {/* search results */}
          <div className="w-ful py-2 h-fit">
            {searchResults && (
              <Searched
                {...searchResults}
                chatPanel={chatPanel}
                friend={searchResults.username}
              />
            )}
          </div>

          {/* FRIEND REQUESTS  */}
          {requestlist.length > 0 && (
            <div className="w-full">
              <h1 className="font-semibold capitalize text-left ">
                friend requests
              </h1>
              {requestlist.map((request, index) => {
                return <Request key={index} {...request} />;
              })}
            </div>
          )}
          {/* recently searched */}
          {recentlySearched.length > 0 && (
            <div className="w-full">
              <h1 className="font-semibold capitalize text-left ">
                recent searches
              </h1>
              {recentlySearched.map((search) => {
                // console.log(search);
                return (
                  <Searched
                    key={search._id}
                    chatPanel={chatPanel}
                    {...search}
                    friend={search.username}
                  />
                );
              })}
            </div>
          )}
          {/* requests recieved */}
          <div className="w-full"></div>
          {/* {sent pending requests } */}
          {pendingreq.length > 0 && (
            <div className="w-full">
              <h4 className="font-semibold capitalize "> pending requests</h4>
              {pendingreq.map((pendreq, i) => {
                return <PendingReq {...pendreq} key={i} />;
              })}
            </div>
          )}
        </section>
      </main>
    </div>
  );
};

export default RequestPage;
