import React from "react";
import self from "../../image/profile.jpg";
import { ArrowLeft, Options } from "../../icons";
import { useSelector } from "react-redux";
import { NavLink } from "react-router-dom";
const Header = () => {
  const { activeFriend } = useSelector((state) => state.chats);
  const { profile } = activeFriend;

  return (
    <header className="w-full flex items-center justify-between py-2 border-b border-gray-600 ">
      {/* PROFILE SECTION  */}
      <div className=" flex gap-2 items-center mx-2">
        {/* BACK BUTTON  */}
        <NavLink to="/" replace className="w-8 h-8 sm:hidden text-txtClr">
          <ArrowLeft />
        </NavLink>
        {/* profile  */}
        <img
          src={
            profile
              ? JSON.parse(profile).secure_url
              : "https://res.cloudinary.com/ddxaqpvsr/image/upload/f_auto,q_auto/v1/chaton/profiles/drqg4ddqgs0kpg3lpqbe"
          }
          // src={self}
          alt=""
          className="w-10 h-10 rounded-full"
        ></img>
        {/* FRIEND NAME  */}
        <article className=" justify-evenly text-sm flex flex-col">
          <h5 className=" font-bold">{activeFriend.friendName}</h5>
          <p className="text-sm ">online</p>
        </article>
      </div>
      <section className="w-8 h-8 text-center mx-2 ">
        <Options />
      </section>
    </header>
  );
};

export default Header;
