import React, { useRef } from "react";
import Hero from "./hero";
import Auth from "./auth";

const AuthPage = () => {
  const homePage = useRef();
  return (
    <div className="h-[100vh] relative transition-all overflow-hidden bg-background w-[100vw]">
      <article
        ref={homePage}
        className="flex absolute left-[0] z-10 bg-transBlack transition-all h-[100vh] aspect-[1/3] w-[300vw]"
      >
        <Hero homePage={homePage} />
        <Auth homePage={homePage} />
      </article>
      {/* <img
        alt=""
        src={bgMobile}
        className="h-[100vh] w-[100vw] pointer-events-none top-0 absolute z-[-10]"
      /> */}
    </div>
  );
};

export default AuthPage;
