import "./App.css";
import MainApp from "./main";
import Localloader from "./loaders/Localloader";
import AuthPage from "./AuthPage";
import { useDispatch, useSelector } from "react-redux";
import GlobalLoader from "./loaders/GlobalLoader";
import "animate.css";
import { useEffect } from "react";

import { verifyUser, logoutUser } from "./store/features/userslice";
import { showErrorModal } from "./store/features/modals";
import { BrowserRouter } from "react-router-dom";
import { setOfflineChats } from "./store/features/chats";

function App() {
  const dispatch = useDispatch();
  const { isLoggedIn } = useSelector((store) => store.user);
  const { errorModal, loadingModal } = useSelector((state) => state.modals);

  // to check if user is present or not
  useEffect(() => {
    const token = localStorage.getItem("token");
    if (token && token.startsWith("Bearer")) {
      dispatch(verifyUser(token));
      dispatch(setOfflineChats());
    } else {
      localStorage.removeItem("token");
      localStorage.removeItem("chats");
      localStorage.removeItem("user");
      dispatch(logoutUser());
    }
  }, []);
  return (
    <BrowserRouter>
      <div className="w-screen h-screen text-txtClr relative overflow-hidden flex flex-col items-center justify-center">
        {errorModal.show && (
          <p className="absolute text-center text-xs md:text-sm bg-transBlack px-4 py-1 rounded-full text-primary z-40 bottom-[15%] animate-slideUp  ">
            {errorModal.message}
          </p>
        )}

        {loadingModal.show && (
          <div className="absolute top-0 z-50 w-screen h-screen flex flex-col items-center gap-4 justify-center bg-[rgba(0,0,0,0.8)]">
            <span className="loader"></span>
            <p className="text-primary capitalize font-semibold">
              {loadingModal.message}
            </p>
          </div>
        )}
        {isLoggedIn ? <MainApp /> : <AuthPage />}
      </div>
    </BrowserRouter>
  );
}

export default App;
