export const generateKeyPair = async () => {
  const private_secret = await bigint(
    Math.round(Math.random() * (9999 - 1000) + 1000)
  ).value;
  const public_secret = await (g ** private_secret % p);
  return {
    public_secret: public_secret.toString(),
    private_secret: private_secret.toString(),
  };
};
export const computeSecret = (friend_public_sec, private_secret) => {
  const secretKey = friend_public_sec ** private_secret % p;
  return secretKey;
};

export const acceptReq = async (
  { sender, acceptor, sender_pk, _id },
  removeReq
) => {
  try {
    const { private_secret, public_secret } = await generateKeyPair();
    const payload = {
      sender,
      acceptor,
      acceptor_pk: public_secret,
      requestId: _id,
    };
    const secret = await computeSecret(
      bigint(sender_pk).value,
      bigint(private_secret).value
    );
    // console.log(public_secret);
    const chat_id = `${sender}${acceptor}`;
    localStorage.setItem(chat_id, secret.toString(16));
    await socket.emit("accept_request", payload, removeReq);
  } catch (error) {
    console.log(error);
  }
};
export const sendFriendReq = async ({ sender, acceptor }, addPendReq) => {
  try {
    const { private_secret, public_secret } = await generateKeyPair();
    console.log("sent a req to ", acceptor);
    const payload = {
      sender,
      acceptor,
      sender_pk: public_secret,
    };
    await socket.emit("send_request", payload, addPendReq);
    localStorage.setItem(`${sender}${acceptor}_secret`, private_secret);
  } catch (error) {
    console.log(error);
  }
};
